import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import { TimePicker } from "carbon-components-react";
import { EventAPI } from "../../../commons/EventAPI";

const dispatchedEvents = [EventAPI.TIME_CHANGED];

/*
    Example: <srs-time label="Time Label"/>
*/
const SrsTime = function (props, ref) {
    const [time, setTime] = useState(props.value || '');

    const handleTimeChange = (evt) => {
        setTime(evt.target.value);
        props.dispatchEvent(EventAPI.TIME_CHANGED, { payload: { value: evt.target.value, id: props.elementid } });
        props.setValue(evt.target.value);
    };

    return (
        <TimePicker
            id={props.elementid}
            labelText={props.label}
            value={time}
            onChange={handleTimeChange}
            size={props.size}
            iconDescription={props.icon_description}
        />
    );
}

const styles = `
    
`
const props = [{
    name: "label",
    defaultValue: "Time",
    type: "string"
}, {
    name: "size",
    defaultValue: "md",
    type: "string"
}, {
    name: "icon_description",
    defaultValue: "",
    type: "string"
}];

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsTime), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };